<template>
    <div class="home_wrap">
        <table>
            <tbody>
                <tr class="row1">
                    <td>
                        <el-row>
                            <el-link v-for="(item, i) in data1" :key="i" @click="goPage(item.routeName)" class="font">{{ item.name }}</el-link>
                        </el-row>
                    </td>
                </tr>
                <tr class="row2">
                    <td>
                        <el-row>
                            <el-link v-for="(item, i) in data2" :key="i" @click="goPage(item.routeName)" class="font">{{ item.name }}</el-link>
                        </el-row>
                    </td>
                </tr>
                <tr class="row3">
                    <td>
                        <el-row>
                            <el-link v-for="(item, i) in data3" :key="i" @click="goPage(item.routeName)" class="font">{{ item.name }}</el-link>
                        </el-row>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        components: {},
        data(){
            return {
                data1: [],
                data2: [],
                data3: [],
            };
        },
        created(){
            this.initData();
        },
        mounted(){},
        methods: {
            // 
            initData(){
                let self = this;
                self.getData1();
                self.getData2();
                self.getData3();
            },
            // data1 
            getData1(){
                let self = this;
                self.data1 = [
                    {name: 'Ueditor ', routeName: 'Ueditor', routePath: '/ueditor'},
                    {name: 'Git', routeName: 'git', routePath: '/it/git'},
                    {name: 'React', routeName: 'react', routePath: '/it/react'},
                    {name: 'codeDemo', routeName: 'codeDemo', routePath: '/it/codeDemo'},
                ];
            },
            // data2 
            getData2(){
                let self = this;
                self.data2 = [
                    {name: ' - ', routeName: 'docStructure', routePath: '/works/docstructure'},
                ];
            },
            // data3 
            getData3(){
                let self = this;
                self.data3 = [
                    {name: '', routeName: 'gongwuyuan', routePath: '/learn/gongwuyuan'},
                ];
            },
            // 
            goPage(pathName){
                let self = this;
                let routeUrl = self.$router.resolve({
                    name: pathName
                });
                window.open(routeUrl.href, '_blank');

                // html   $route.query.id
                // script   this.$route.query.id

                // vue
                // 1. router-link
                // 2. this.$router.push() ()
                // 3. this.$router.replace() (push)
                // 4. this.$router.go(n)
            },

        },
    };
</script>

<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.home_wrap{
        height: 100%; position: relative; text-align: center;
        table{ height: auto; min-height: calc(100vh - 120px); width: 100%; }
        .row1{ background-color: #fddbe2; }
        .row2{ background-color: #f9c6d2; }
        .row3{ background-color: #f7b1c2; }
        .font{
            background-color: #f71f52;
            border-radius: 3px;
            display: inline-block;
            padding: 2px 8px;
            margin: 3px 10px;
            font-size: 18px;
            text-decoration: none;
            color: #fddbe2;
        }
}
</style>



